// src/pages/hr/applicants.js
import { useState, useEffect, useRef } from "react";
import { useRouter } from "next/router";
import toast, { Toaster } from "react-hot-toast";
import HRSidebar from "@/layouts/hrSidebar";
import HRHeader from "@/layouts/hrHeader";
import useSidebar from "@/hooks/useSidebar";
import { supabaseServer } from "@/lib/supabaseServer";
import { Icon } from "@iconify/react";
import SimpleFooter from "@/layouts/simpleFooter";
import ApplicantsTable from "@/components/ApplicantsTable";
import ApplicantsFilters from "@/components/ApplicantsFilters";
import dynamic from "next/dynamic";

// Dynamically import Editor.js to avoid SSR issues
const EditorJS = dynamic(() => import("@editorjs/editorjs"), { ssr: false });

export default function HRApplicants({ mode = "light", toggleMode }) {
    const [candidates, setCandidates] = useState([]);
    const [filteredCandidates, setFilteredCandidates] = useState([]);
    const [sortField, setSortField] = useState("full_name");
    const [sortDirection, setSortDirection] = useState("asc");
    const { isSidebarOpen, toggleSidebar } = useSidebar();
    const router = useRouter();
    const [selectedCandidate, setSelectedCandidate] = useState(null);
    const [isModalOpen, setIsModalOpen] = useState(false);
    const [isEmailModalOpen, setIsEmailModalOpen] = useState(false);
    const [emailData, setEmailData] = useState({ subject: "", body: "" });
    const editorRef = useRef(null);

    useEffect(() => {
        if (!localStorage.getItem("hr_session")) {
            router.push("/hr/login");
        } else {
            fetchCandidates();
        }
    }, [router]);

    const fetchCandidates = async () => {
        console.log("Fetching candidates with supabaseServer...");
        try {
            const { data: candidatesData, error: candidatesError } = await supabaseServer
                .from("candidates")
                .select("id, full_name, email, phone, linkedin, opening");
            if (candidatesError) throw candidatesError;

            const { data: responsesData, error: responsesError } = await supabaseServer
                .from("responses")
                .select("user_id, answers, score, resume_url, cover_letter_url, status");
            if (responsesError) throw responsesError;

            const { data: questionsData, error: questionsError } = await supabaseServer
                .from("interview_questions")
                .select("id, text")
                .order("order", { ascending: true });
            if (questionsError) throw questionsError;

            const combinedData = candidatesData.map((candidate) => {
                const response = responsesData.find((r) => r.user_id === candidate.id) || {};
                return {
                    ...candidate,
                    answers: Array.isArray(response.answers) ? response.answers : [],
                    score: response.score || 0,
                    resumeUrl: response.resume_url,
                    coverLetterUrl: response.cover_letter_url,
                    status: response.status || "Pending",
                    questions: questionsData,
                };
            });
            setCandidates(combinedData);
            setFilteredCandidates(combinedData);
        } catch (error) {
            console.error("Error fetching candidates:", error);
            toast.error("Failed to load candidates.");
        }
    };

    const handleLogout = () => {
        localStorage.removeItem("hr_session");
        toast.success("Logged out successfully!");
        setTimeout(() => router.push("/hr/login"), 1000);
    };

    const handleViewCandidate = (candidate) => {
        setSelectedCandidate(candidate);
        setIsModalOpen(true);
    };

    const handleCloseModal = () => {
        setIsModalOpen(false);
        setSelectedCandidate(null);
    };

    const handleFilterChange = ({ searchQuery, filterOpening, filterStatus }) => {
        let result = [...candidates];
        if (searchQuery) {
            result = result.filter(
                (c) =>
                    c.full_name.toLowerCase().includes(searchQuery.toLowerCase()) ||
                    c.email.toLowerCase().includes(searchQuery.toLowerCase())
            );
        }
        if (filterOpening !== "all") {
            result = result.filter((c) => c.opening === filterOpening);
        }
        if (filterStatus !== "all") {
            result = result.filter((c) => c.status === filterStatus);
        }
        setFilteredCandidates(result);
    };

    const handleSort = (field) => {
        const newDirection = sortField === field && sortDirection === "asc" ? "desc" : "asc";
        setSortField(field);
        setSortDirection(newDirection);

        const sorted = [...filteredCandidates].sort((a, b) => {
            const aValue = a[field] || "";
            const bValue = b[field] || "";
            if (field === "score") {
                return newDirection === "asc" ? aValue - bValue : bValue - aValue;
            }
            return newDirection === "asc"
                ? aValue.localeCompare(bValue)
                : bValue.localeCompare(aValue);
        });
        setFilteredCandidates(sorted);
    };

    const handleStatusChange = async (candidateId, newStatus) => {
        try {
            const candidate = candidates.find((c) => c.id === candidateId);
            const answers = candidate.answers.length > 0 ? candidate.answers : [];

            const { error } = await supabaseServer
                .from("responses")
                .upsert(
                    {
                        user_id: candidateId,
                        answers: answers,
                        status: newStatus,
                        score: candidate.score || 0,
                    },
                    { onConflict: ["user_id"] }
                )
                .eq("user_id", candidateId);
            if (error) throw error;

            const updatedCandidates = candidates.map((c) =>
                c.id === candidateId ? { ...c, status: newStatus } : c
            );
            setCandidates(updatedCandidates);
            setFilteredCandidates(updatedCandidates);
            setSelectedCandidate((prev) => ({ ...prev, status: newStatus }));

            toast.success("Status updated successfully!", { icon: "✅" });

            if (["Reviewed", "Shortlisted", "Rejected"].includes(newStatus)) {
                const sendEmail = window.confirm(`Send email to ${candidate.full_name} about their ${newStatus} status?`);
                if (sendEmail) {
                    const response = await fetch(
                        `/api/get-email-template?status=${newStatus}&fullName=${encodeURIComponent(candidate.full_name)}&opening=${encodeURIComponent(candidate.opening)}`
                    );
                    const { template } = await response.json();
                    if (!response.ok) throw new Error("Failed to fetch email template");

                    setEmailData({
                        subject: {
                            Reviewed: `Application Reviewed for ${candidate.opening} - Growthpad Consulting Group`,
                            Shortlisted: `Congratulations! You’ve Been Shortlisted for ${candidate.opening} - Growthpad Consulting Group`,
                            Rejected: `Application Update for ${candidate.opening} - Growthpad Consulting Group`,
                        }[newStatus],
                        body: template,
                    });
                    setIsEmailModalOpen(true);
                }
            }
        } catch (error) {
            console.error("Error updating status:", error);
            toast.error("Failed to update status.");
        }
    };

    const handleSendEmail = async () => {
        try {
            const response = await fetch("/api/send-status-email", {
                method: "POST",
                headers: { "Content-Type": "application/json" },
                body: JSON.stringify({
                    fullName: selectedCandidate.full_name,
                    email: selectedCandidate.email,
                    opening: selectedCandidate.opening,
                    status: selectedCandidate.status,
                }),
            });
            const result = await response.json();
            if (!response.ok) throw new Error(result.error || "Failed to send email");

            toast.success("Email sent successfully!", { icon: "✅" });
            setIsEmailModalOpen(false);
        } catch (error) {
            console.error("Error sending email:", error);
            toast.error("Failed to send email.");
        }
    };

    const initializeEditor = (initialData) => {
        if (editorRef.current) editorRef.current.destroy();

        editorRef.current = new EditorJS({
            holder: "editorjs",
            tools: {
                header: {
                    class: require("@editorjs/header"),
                    inlineToolbar: true,
                },
                list: {
                    class: require("@editorjs/list"),
                    inlineToolbar: true,
                },
                paragraph: {
                    class: require("@editorjs/paragraph"),
                    inlineToolbar: true,
                },
            },
            data: {
                blocks: initialData ? JSON.parse(initialData) : [
                    {
                        type: "paragraph",
                        data: { text: initialData || "" },
                    },
                ],
            },
            onChange: async () => {
                const content = await editorRef.current.save();
                const html = content.blocks.map(block => {
                    switch (block.type) {
                        case "header":
                            return `<h${block.data.level}>${block.data.text}</h${block.data.level}>`;
                        case "list":
                            const items = block.data.items.map(item => `<li>${item}</li>`).join("");
                            return block.data.style === "ordered" ? `<ol>${items}</ol>` : `<ul>${items}</ul>`;
                        case "paragraph":
                            return `<p>${block.data.text}</p>`;
                        default:
                            return "";
                    }
                }).join("");
                setEmailData((prev) => ({ ...prev, body: html }));
            },
        });
    };

    useEffect(() => {
        if (isEmailModalOpen && emailData.body) {
            initializeEditor(emailData.body);
        }
    }, [isEmailModalOpen, emailData.body]);

    return (
        <div
            className={`min-h-screen flex flex-col ${
                mode === "dark" ? "bg-gradient-to-b from-gray-900 to-gray-800" : "bg-gradient-to-b from-gray-50 to-gray-100"
            }`}
        >
            <Toaster position="top-right" reverseOrder={false} />
            <HRHeader
                toggleSidebar={toggleSidebar}
                isSidebarOpen={isSidebarOpen}
                mode={mode}
                toggleMode={toggleMode}
                onLogout={handleLogout}
            />
            <div className="flex flex-1">
                <HRSidebar
                    isOpen={isSidebarOpen}
                    mode={mode}
                    onLogout={handleLogout}
                    toggleSidebar={toggleSidebar}
                />
                <div
                    className={`flex-1 p-6 transition-all duration-300 ${
                        isSidebarOpen ? "md:ml-[300px]" : "md:ml-[80px]"
                    }`}
                >
                    <div className="max-w-6xl mx-auto">
                        <h2
                            className={`text-2xl font-bold mb-6 ${
                                mode === "dark" ? "text-white" : "text-[#231812]"
                            }`}
                        >
                            Applicants
                        </h2>
                        <ApplicantsFilters candidates={candidates} onFilterChange={handleFilterChange} mode={mode} />
                        <ApplicantsTable
                            candidates={filteredCandidates}
                            mode={mode}
                            onViewCandidate={handleViewCandidate}
                            onSort={handleSort}
                            sortField={sortField}
                            sortDirection={sortDirection}
                        />
                    </div>
                </div>
            </div>

            {/* Candidate Details Modal */}
            {isModalOpen && selectedCandidate && (
                <div className="fixed inset-0 bg-black bg-opacity-60 flex justify-center items-center z-50">
                    <div className="bg-white rounded-xl max-w-3xl w-full mx-4 shadow-2xl transform transition-all duration-300 animate-fade-in flex flex-col max-h-[90vh]">
                        <div className="bg-gradient-to-r from-[#f05d23] to-[#d94f1e] rounded-t-xl p-4 flex items-center justify-between">
                            <div className="flex items-center">
                                <Icon icon="mdi:account" className="w-8 h-8 text-white mr-3" />
                                <h2 className="text-2xl font-bold text-white">{selectedCandidate.full_name}</h2>
                            </div>
                            <button
                                onClick={() => setIsModalOpen(false)}
                                className="text-white hover:text-gray-200 transition duration-200"
                            >
                                <Icon icon="mdi:close" width={24} height={24} />
                            </button>
                        </div>
                        <div className="flex-1 p-6 overflow-y-auto">
                            <div className="space-y-4">
                                <p><strong>Email:</strong> {selectedCandidate.email}</p>
                                <p><strong>Phone:</strong> {selectedCandidate.phone}</p>
                                <p>
                                    <strong>LinkedIn:</strong>{" "}
                                    <a href={selectedCandidate.linkedin} target="_blank" className="text-[#f05d23]">
                                        {selectedCandidate.linkedin}
                                    </a>
                                </p>
                                <p><strong>Opening:</strong> {selectedCandidate.opening}</p>
                                <p>
                                    <strong>Score:</strong>{" "}
                                    {selectedCandidate.score}/{selectedCandidate.questions.length * 10} (
                                    {Math.round((selectedCandidate.score / (selectedCandidate.questions.length * 10)) * 100)}%)
                                </p>
                                <div>
                                    <h3 className="text-lg font-semibold mb-2">Documents</h3>
                                    <div className="flex gap-4">
                                        {selectedCandidate.resumeUrl && (
                                            <a href={selectedCandidate.resumeUrl} target="_blank" className="text-[#f05d23] hover:underline">
                                                <Icon icon="mdi:file-document" className="inline w-5 h-5 mr-1" /> Resume
                                            </a>
                                        )}
                                        {selectedCandidate.coverLetterUrl && (
                                            <a href={selectedCandidate.coverLetterUrl} target="_blank" className="text-[#f05d23] hover:underline">
                                                <Icon icon="mdi:file-document" className="inline w-5 h-5 mr-1" /> Cover Letter
                                            </a>
                                        )}
                                    </div>
                                </div>
                                <div>
                                    <h3 className="text-lg font-semibold mb-2">Interview Answers</h3>
                                    {(selectedCandidate.answers || []).map((answer, idx) => (
                                        <div key={idx} className="mb-4">
                                            <p className="font-semibold">
                                                {selectedCandidate.questions[idx]?.text || `Question ${idx + 1}`}
                                            </p>
                                            <p>{Array.isArray(answer) ? answer.join(", ") : answer || "No answer"}</p>
                                        </div>
                                    ))}
                                </div>
                                <div>
                                    <h3 className="text-lg font-semibold mb-2">Status</h3>
                                    <select
                                        value={selectedCandidate.status || "Pending"}
                                        onChange={(e) => handleStatusChange(selectedCandidate.id, e.target.value)}
                                        className="p-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-[#f05d23]"
                                    >
                                        {["Pending", "Reviewed", "Shortlisted", "Rejected"].map((status) => (
                                            <option key={status} value={status}>
                                                {status}
                                            </option>
                                        ))}
                                    </select>
                                </div>
                            </div>
                        </div>
                        <div className="sticky bottom-0 bg-white p-4 border-t border-gray-200 rounded-b-xl shadow-md">
                            <div className="flex justify-end gap-4">
                                <button
                                    onClick={() => setIsModalOpen(false)}
                                    className="px-6 py-2 bg-gray-200 text-[#231812] rounded-full hover:bg-gray-300 transition duration-200 flex items-center gap-2 shadow-md"
                                >
                                    <Icon icon="mdi:close" width={20} height={20} />
                                    Close
                                </button>
                            </div>
                        </div>
                    </div>
                </div>
            )}

            {/* Email Modal */}
            {isEmailModalOpen && selectedCandidate && (
                <div className="fixed inset-0 bg-black bg-opacity-60 flex justify-center items-center z-50">
                    <div className="bg-white rounded-xl max-w-3xl w-full mx-4 shadow-2xl transform transition-all duration-300 animate-fade-in flex flex-col max-h-[90vh]">
                        <div className="bg-gradient-to-r from-[#f05d23] to-[#d94f1e] rounded-t-xl p-4 flex items-center justify-between">
                            <div className="flex items-center">
                                <Icon icon="mdi:email" className="w-8 h-8 text-white mr-3" />
                                <h2 className="text-2xl font-bold text-white">Send Email to {selectedCandidate.full_name}</h2>
                            </div>
                            <button
                                onClick={() => setIsEmailModalOpen(false)}
                                className="text-white hover:text-gray-200 transition duration-200"
                            >
                                <Icon icon="mdi:close" width={24} height={24} />
                            </button>
                        </div>
                        <div className="flex-1 p-6 overflow-y-auto">
                            <div className="space-y-4">
                                <div>
                                    <label className="block text-sm font-medium mb-1 text-[#231812]">To</label>
                                    <input
                                        type="email"
                                        value={selectedCandidate.email}
                                        readOnly
                                        className="w-full p-2 border rounded-lg bg-gray-100 text-[#231812]"
                                    />
                                </div>
                                <div>
                                    <label className="block text-sm font-medium mb-1 text-[#231812]">Subject</label>
                                    <input
                                        type="text"
                                        value={emailData.subject}
                                        onChange={(e) => setEmailData({ ...emailData, subject: e.target.value })}
                                        className="w-full p-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-[#f05d23]"
                                    />
                                </div>
                                <div>
                                    <label className="block text-sm font-medium mb-1 text-[#231812]">Body</label>
                                    <div id="editorjs" className="border rounded-lg p-2"></div>
                                </div>
                            </div>
                        </div>
                        <div className="sticky bottom-0 bg-white p-4 border-t border-gray-200 rounded-b-xl shadow-md">
                            <div className="flex justify-end gap-4">
                                <button
                                    onClick={() => setIsEmailModalOpen(false)}
                                    className="px-6 py-2 bg-gray-200 text-[#231812] rounded-full hover:bg-gray-300 transition duration-200 flex items-center gap-2 shadow-md"
                                >
                                    <Icon icon="mdi:close" width={20} height={20} />
                                    Cancel
                                </button>
                                <button
                                    onClick={handleSendEmail}
                                    className="px-6 py-2 bg-[#f05d23] text-white rounded-full hover:bg-[#d94f1e] transition duration-200 flex items-center gap-2 shadow-md"
                                >
                                    <Icon icon="mdi:send" width={20} height={20} />
                                    Send Email
                                </button>
                            </div>
                        </div>
                    </div>
                </div>
            )}

            <SimpleFooter mode={mode} />
        </div>
    );
}